import Vue from 'vue'
import Moment from 'moment'

Vue.filter('toData', function (val) {
    return Moment(val).format('YYYY-MM-DD')
})
Vue.filter('toDataDetails', function (val) {
    return Moment(val).format('YYYY-MM-DD HH:mm:ss')
})
Vue.filter('toType', function (val) {
    if (!val) return false
    let currentVal = Number(val)
    let visitsName = ''
    switch (currentVal) {
        case 1:
            visitsName = 'TV'
            break
        case 2:
            visitsName = ''
            break
        case 3:
            visitsName = ''
            break
        case 4:
            visitsName = ''
            break
        case 5:
            visitsName = ''
            break
        case 6:
            visitsName = ''
            break
        case 7:
            visitsName = ''
            break
        case 8:
            visitsName = 'MG'
            break
        case 9:
            visitsName = ''
            break
        case 10:
            visitsName = ''
            break
        case 11:
            visitsName = 'VCR'
            break
        default:
            visitsName = ''
            break
    }
    return visitsName
})
